import events from "@/utils/events"
import common from "@/utils/common"

export default {
    created() {
        this.setEventHandler()
        if (!this.$store.state.loading) {
            this.baseInit()
        }
    },
    beforeDestroy() {
    },
    data() {
        return {
            eventHandlers: {},
            roles: {
                sender: 'payer',
                receiver: 'payee',
            }
        }
    },
    computed: {
        initLoading() {
            return this.$store.state.loading
        },
        screenWidth() {
            return this.$store.state.screenWidth
        },
        isMobile() {
            return this.$store.state.screenWidth < 960
        },
        account() {
            return this.$store.state.account
        },
        currentRoute() {
            return this.$store.state.currentRoute
        },
        currentRouteName() {
            return this.$store.state.currentRouteName
        },
        factoryInviter() {
            return this.$store.state.factory.inviter
        },
        donateItem() {
            return this.$store.state.donate
        },
        poolItem() {
            return this.$store.state.tradeInfo.poolItem
        },
        canSendTx() {
            return null !== this.account && null !== this.poolItem
        },
        dtokenDecimals() {
            if (null === this.$store.state.dtoken) {
                return 0
            }
            return this.$store.state.dtoken.decimals
        },
        dtokenSymbol() {
            if (null === this.$store.state.dtoken) {
                return 'dToken'
            }
            return this.$store.state.dtoken.symbol
        },
        dtokenAddress() {
            if (null === this.$store.state.dtoken) {
                return '0x'
            }
            return this.$store.state.dtoken.address
        },
        tokenItem() {
            if (null === this.poolItem) {
                return null
            }
            return this.$store.state.tradeInfo.poolItem.token
        },
        tokenSymbol() {
            if (null === this.poolItem) {
                return 'Token'
            }
            return this.tokenItem.symbol
        },
        tokenDecimals() {
            if (null === this.poolItem) {
                return 0
            }
            return this.tokenItem.decimals.toNumber()
        },
        tokenAddress() {
            if (null === this.poolItem) {
                return '0x'
            }
            return this.tokenItem.address
        },
        tokenBalanceInUnit() {
            if (null === this.poolItem) {
                return 0
            }
            return this.poolItem.token.balanceInUnit
        },
        tokenBalance() {
            if (null == this.poolItem) {
                return common.toBigNumber(0)
            }
            return this.poolItem.token.balance
        },
        tokenAllowance() {
            if (null === this.poolItem) {
                return common.toBigNumber(0)
            }
            return this.poolItem.token.allowance
        },
        tradeInfo() {
            return this.$store.state.tradeInfo
        },
    },
    methods: {
        baseInit() {
        },
        setEventHandler() {
            this.$eventBus.$on(events.inited, () => {
                this.baseInit()
            })
            this.$eventBus.$on(events.txSent, params => {
                const handler = this.eventHandlers[events.txSent]
                if (handler) {
                    handler(params)
                }
            })
            this.$eventBus.$on(events.txConfirmed, params => {
                const handler = this.eventHandlers[events.txConfirmed]
                if (handler) {
                    handler(params)
                }
            })
            this.$eventBus.$on(events.tokenChanged, params => {
                const handler = this.eventHandlers[events.tokenChanged]
                if (handler) {
                    handler(params)
                }
            })
            this.$eventBus.$on(events.dTokenInited, params => {
                const handler = this.eventHandlers[events.dTokenInited]
                if (handler) {
                    handler(params)
                }
            })
            this.$eventBus.$on(events.payeeChanged, params => {
                const handler = this.eventHandlers[events.payeeChanged]
                if (handler) {
                    handler(params)
                }
            })
        },
        getExplorerUrl(type, info) {
            return common.getExplorerUrl(type, info)
        },
        formatAddress(address) {
            let first = address.substring(0, 6)
            let mid = '...'
            let last = address.substring(address.length - 4, address.length)
            return first + mid + last
        },
        copyText(text) {
            this.$copyText(text).then(() => {
                this.$message({
                    showClose: true,
                    message: this.$t('common.copySuccess'),
                    type: 'success'
                })
            })
        },
        goDc2cHome() {
            window.open('https://dc2c.finance', '_blank')
        },
        goDocs() {
            let url = 'https://zh.docs.dc2c.finance/'
            if (this.$i18n.locale === 'en') {
                url = 'https://en.docs.dc2c.finance/'
            }
            window.open(url, '_blank')
        },
        goDevelopers() {
            window.open('https://github.com/dc2c', '_blank')
        },
        goGovernance() {
            let url = 'https://zh.docs.dc2c.finance/zhi-li/zhi-li-fang-an'
            if (this.$i18n.locale === 'en') {
                url = 'https://en.docs.dc2c.finance/governance-and-maintenance/governance-plan'
            }
            window.open(url, '_blank')
        },
        goInstallWallet() {
            window.open('https://token.im/', '_blank')
        }
    }
}
