<template>
  <div id="app">
    <main-panel/>
  </div>
</template>

<script>

import common from "@/utils/common"
import metamask from "@/utils/metamask"
import store from "@/store"
import db from "@/utils/db"
import DC2CDonate from "@/abi/DC2CDonate.json"
import SecTxsFactory from "@/abi/DC2CFactory.json"
import Erc20 from "@/abi/Erc20.json"
import SecTxsPool from "@/abi/DC2CPool.json"
import erc20 from "@/utils/erc20"
import events from "@/utils/events"
import factory from "@/utils/factory"
import base from "@/utils/base"
import DToken from "@/abi/DToken.json";
import tp from "tp-js-sdk";

const MainPanel = () => import('@/views/main_panel.vue')
export default {
  mixins: [base],
  name: 'App',
  components: {
    MainPanel
  },
  computed: {
    loading() {
      return this.$store.state.loading
    },
  },
  data() {
    return {
    }
  },
  created() {
    let inviter = common.getQueryParam('inviter')
    if (null !== common.getAddress(inviter)) {
      this.$store.commit('setTradeInviter', inviter)
    }
    const screenWidth = document.body.clientWidth
    this.$store.commit('setScreenWidth', screenWidth)
    window.onresize = () => {
      const screenWidth = document.body.clientWidth
      this.$store.commit('setScreenWidth', screenWidth)
    }
    this.init()
  },
  methods: {
    async init() {
      await store.commit('setLoading', true)
      await this.networkInit()
      await this.dbInit()
      if (this.account) {
        await this.factoryInit()
        await this.poolListInit()
        this.eventHandlers[events.tokenChanged] = () => {
          this.dtokenInit(this.poolItem)
        }
      } else {
        await store.commit('setLoading', false)
      }
      setInterval(this.txSyncTimer, 5000)
    },
    async networkInit() {

      const provider = metamask.getProvider()
      if (null === provider) {
        return
      }

      await this.$store.commit('setProvider', provider)

      metamask.setChainChangedEvent(() => {
        window.location.reload()
      })
      metamask.setAccountsChangedEvent(() => {
        window.location.reload()
      })

      //get netVersion
      // let netVersion = ''
      // let account = ''
      // if (tp.isConnected()) {
      //   const wallet = await tp.getCurrentWallet()
      //   switch (wallet.data.blockchain) {
      //     case 'eth':
      //       netVersion = '1'
      //       break
      //     case 'bsc':
      //       netVersion = '56'
      //       break
      //     case 'heco':
      //       netVersion = '128'
      //       break
      //     case -1:
      //       netVersion = '3'
      //       break
      //   }
      //   account = wallet.data.address
      //
      //   if (netVersion !== parseInt(window.ethereum.chainId, 16).toString()) {
      //     await metamask.switchEthereumChain(netVersion)
      //   }
      //
      //   let t = await metamask.netVersion()
      //   this.$message.info(t)
      //   let ts = await metamask.selectedAddress()
      //   if (ts.length > 0) {
      //     this.$message.info(ts[0])
      //   }
      //
      // } else {
      //   netVersion = await metamask.netVersion()
      //   const accounts = await metamask.selectedAddress()
      //   if (accounts.length > 0) {
      //     account = accounts[0]
      //   }
      // }

      let netVersion = await metamask.netVersion()

      if (tp.isConnected()) {

        let tpNetversion
        const wallet = await tp.getCurrentWallet()
        switch (wallet.data.blockchain) {
          case 'eth':
            tpNetversion = '1'
            break
          case 'bsc':
            tpNetversion = '56'
            break
          case 'heco':
            tpNetversion = '128'
            break
          case 'matic':
            tpNetversion = '137'
            break
          case -1:
            tpNetversion = '3'
            break
        }
        if (tpNetversion !== netVersion) {
          await metamask.switchEthereumChain(tpNetversion)
          netVersion = await metamask.netVersion()
        }
      }

      const accounts = await metamask.selectedAddress()
      let account = ''
      if (accounts.length > 0) {
        account = accounts[0]
      }

      await this.$store.commit('setNetVersion', netVersion.toString())
      await this.$store.commit('setAccount', account)
    },
    async factoryInit() {
      const factoryAddress = common.getFactoryAddress()
      if (factoryAddress === '') {
        return
      }

      const donateAddress = common.getDonateAddress()

      const donateInst = common.getContractInst(donateAddress, DC2CDonate)
      await this.$store.commit('setDonateInst', donateInst)

      const secTxsPoolFactory = common.getContractInst(factoryAddress, SecTxsFactory)
      await this.$store.commit('setFactoryInst', secTxsPoolFactory)

      const factoryConfig = await factory.config()
      await this.$store.commit('setFactoryConfig', factoryConfig)

      const inviter = await factory.getInviter(this.account)
      await this.$store.commit('setFactoryInviter', inviter)
    },
    async poolListInit() {
      // get pools from factory
      if (this.$store.state.factory.contract === null) {
        return
      }
      const tokenSize = await factory.tokenSize()

      const poolList = []
      if (tokenSize > 0) {

        const tokens = common.getConfigTokens()
        const tokenMap = {}
        if (tokens) {
          tokens.forEach(i => {
            tokenMap[i.address.toLowerCase()] = i
          })
        }

        const list = await factory.getTokens(0, tokenSize)
        for (let i = 0; i < list.length; i++) {

          const token = {
            name: '',
            symbol: '',
            decimals: 0,
            address: list[i],
            balance: common.toBigNumber(0),
            balanceInUnit: 0,
            allowance: common.toBigNumber(0),
            contract: null,
            icon: '',
          }
          if (tokenMap[token.address.toLowerCase()]) {
            token.name = tokenMap[token.address.toLowerCase()].name
            token.symbol = tokenMap[token.address.toLowerCase()].symbol
            token.decimals = common.toBigNumber(tokenMap[token.address.toLowerCase()].decimals)
            token.icon = tokenMap[token.address.toLowerCase()].iconUrl
          }

          const pool = {
            address: '',
            contract: null,
            token: token
          }

          poolList.push(pool)
        }
      }

      await this.$store.commit('setPoolList', poolList)

      this.poolTokenInit()
    },
    async poolTokenInit() {
      const poolList = this.$store.state.poolList
      for (let i = 0; i < poolList.length; i++) {

        let poolInst = poolList[i]

        if (poolInst.token.contract === null) {

          let poolAddress = await factory.getDC2CPool(poolInst.token.address)

          //init erc20 token
          let tokenContract = common.getContractInst(poolInst.token.address, Erc20)

          let name = poolInst.token.name
          let symbol = poolInst.token.symbol
          let decimals = poolInst.token.decimals
          let icon = poolInst.token.icon
          if (poolInst.token.name === '') {
            name = await tokenContract.name()
            symbol = await tokenContract.symbol()
            decimals = await tokenContract.decimals()
          }

          let balance = await tokenContract.balanceOf(this.account)
          let balanceInUnit = common.weiToUnitWithDecimals(balance, decimals)
          let allowance = await tokenContract.allowance(this.account, poolAddress)
          const token = {
            name: name,
            symbol: symbol,
            decimals: decimals,
            address: poolInst.token.address,
            balance: balance,
            balanceInUnit: balanceInUnit,
            allowance: allowance,
            contract: tokenContract,
            icon: icon,
          }

          let poolContract = common.getContractInst(poolAddress, SecTxsPool)
          poolInst = {
            address: poolAddress,
            contract: poolContract,
            token: token,
          }

          this.$store.commit('setPoolByTokenAddress', poolInst)

          if (i === 0) {
            this.selectedTokenInit(poolInst)
          }
        }
      }
    },
    async selectedTokenInit(poolInst) {
      await this.dtokenInit(poolInst)
      await common.onTokenSelected(poolInst)
      await store.commit('setLoading', false)
      this.$eventBus.$emit(events.inited)
    },
    async dtokenInit(poolInst) {
      const dtokenAddress = await factory.getDToken(poolInst.token.address)
      const dtokenContract = common.getContractInst(dtokenAddress, DToken)
      const dtokenSymbol = await dtokenContract.symbol()
      const dtokenDecimals = await dtokenContract.decimals()
      await this.$store.commit('setDTokenItem', {
        address: dtokenAddress,
        contract: dtokenContract,
        symbol: dtokenSymbol,
        decimals: dtokenDecimals,
      })
      this.$eventBus.$emit(events.dTokenInited)
    },
    async dbInit() {
      await db.loadToStore()
    },
    async txSyncTimer() {
      let updateList = []

      for (const item of store.state.transactions) {
        if (!item.receipt || !item.receipt.blockNumber) {
          const t = await this.$store.state.provider.getTransactionReceipt(item.hash)
          if (t) {
            item.receipt = {
              blockNumber: t.blockNumber,
              status: t.status,
              type: t.type,
            }
            updateList.push(item)

            switch (item.type) {
              case common.getTxTypes().approve:
                await erc20.syncTokenAllowance()
                break
              case common.getTxTypes().depositMargin:
                await erc20.syncTokenBalance()
                break
              case common.getTxTypes().withdrawMargin:
                await erc20.syncTokenBalance()
                break
              case common.getTxTypes().donate:
                await erc20.syncTokenBalance()
                break
              case common.getTxTypes().create:
                break
              case common.getTxTypes().confirmReceived:
                await erc20.syncTokenBalance()
                break
              case common.getTxTypes().confirmNotDelivered:
                await erc20.syncTokenBalance()
                break
              case common.getTxTypes().createPool:
                await this.poolListInit()
                break
            }
            this.$eventBus.$emit(events.txConfirmed, {
              hash: item.hash,
              type: item.type
            })
          }
        }
      }

      for (const item of updateList) {
        await db.addTransaction(item)
      }
    }
  }
}
</script>

