import {ethers} from "ethers"

export default {
    getProvider() {
        if (typeof window.ethereum === 'undefined') {
            return null
        }
        return new ethers.providers.Web3Provider(window.ethereum)
    },
    netVersion() {
        return window.ethereum.request({method: 'net_version'})
    },
    selectedAddress() {
        return window.ethereum.request({method: 'eth_accounts'})
    },
    switchEthereumChain(chainId) {
        return window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: chainId}],
        })
    },
    addEthereumChain(chainId, chainName, currencyName, currencySymbol, currencyDecimals, rpcUrls, blockExplorerUrls, iconUrls) {
        return window.ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [
                {
                    chainId: chainId,
                    chainName: chainName,
                    nativeCurrency: {
                        name: currencyName,
                        symbol: currencySymbol,
                        decimals: currencyDecimals,
                    },
                    rpcUrls: rpcUrls,
                    blockExplorerUrls: blockExplorerUrls,
                    iconUrls: iconUrls,
                }
            ],
        })
    },
    setChainChangedEvent(func) {
        window.ethereum.on('chainChanged', func)
    },
    setAccountsChangedEvent(func) {
        window.ethereum.on('accountsChanged', func)
    },
    async getAccount() {
        let account = ''
        const accounts = await window.ethereum.request({method: 'eth_requestAccounts'});
        if (accounts.length > 0) {
            account = accounts[0]
        }
        return account
    },
    watchAsset(token) {
        return window.ethereum.request({
            method: 'wallet_watchAsset',
            params: {
                type: 'ERC20',
                options: {
                    address: token.address,
                    symbol: token.symbol,
                    decimals: token.decimals,
                }
            }
        })
    }
}
