import store from '@/store/index'

const namespace = 'sectxs_app'
const db = {}

db.getNetworkVersionNamespace = () => {
    return `${namespace}_${store.state.netVersion}`
}

db.saveData = (data) => {
    localStorage.setItem(db.getNetworkVersionNamespace(), JSON.stringify(data))
}

db.getData = () => {
    let text = localStorage.getItem(db.getNetworkVersionNamespace())
    if (text) {
        const data = JSON.parse(text)
        return data
    } else {
        const r = {
            t: []
        }
        db.saveData(r)
        return r
    }
}

db.getTransactions = () => {
    return db.getData().t
}

db.loadToStore = async () => {
    const data = db.getData()
    await store.commit('setTransactions', data.t)

    await store.commit('setTradeSetting', db.getSetting())
}

db.clearData = async () => {
    const data = {
        t: []
    }
    db.saveData(data)
    await store.commit('setTransactions', data.t)
}

db.addTransaction = async (tx) => {

    let txs = db.getTransactions()
    let index = -1
    for (let i = 0; i < txs.length; i++) {
        if (txs[i].hash === tx.hash) {
            index = i
            break
        }
    }
    if (index === -1) {
        txs.push(tx)
    } else {
        txs.splice(index, 1, tx)
    }

    db.saveData({t: txs})
    store.commit('setTransactions', txs)
}

db.getSetting = () => {
    const key = namespace + '_setting'
    let text = localStorage.getItem(key)
    if (text) {
        const data = JSON.parse(text)
        return data
    } else {
        return null
    }
}

db.updateSetting = async setting => {
    const key = namespace + '_setting'
    localStorage.setItem(key, JSON.stringify(setting))
    await store.commit('setTradeSetting', setting)
}

export default db
