import Vue from 'vue'
import Vuex from 'vuex'
import common from "@/utils/common";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    screenWidth: 0,
    loading: true,
    provider: null,
    netVersion: '',
    account: '',
    currentRoute: '/',
    currentRouteName: '',
    factory: {
      inviter: '',
      config: null,
      contract: null,
    },
    donate: {
      contract: null,
    },
    dtoken: null,
    poolList: [],
    tradeInfo: {
      inviter: '',
      setting: null,
      poolItem: null,
      marginBalance: null,
    },
    transactions: [],
    confirmedTxType: '',
  },
  mutations: {
    setScreenWidth(state, screenWidth) {
      state.screenWidth = screenWidth
    },
    setLoading(state, loading) {
      state.loading = loading
    },
    setProvider(state, provider) {
      state.provider = provider
    },
    setNetVersion(state, netVersion) {
      state.netVersion = netVersion
    },
    setAccount(state, account) {
      state.account = account
    },
    setCurrentRoute(state, currentRoute) {
      state.currentRoute = currentRoute
    },
    setCurrentRouteName(state, currentRouteName) {
      state.currentRouteName = currentRouteName
    },
    setPoolByTokenAddress(state, pool) {
      state.poolList.forEach(item => {
        if (item.token.address.toLowerCase() === pool.token.address.toLowerCase()) {
          common.objCopy(pool, item)
        }
      })
    },
    setDTokenItem(state, dtokenItem) {
      state.dtoken = dtokenItem
    },
    setFactoryInst(state, factoryInst) {
      state.factory.contract = factoryInst
    },
    setFactoryInviter(state, inviter) {
      state.factory.inviter = inviter
    },
    setFactoryConfig(state, config) {
      state.factory.config = config
    },
    setDonateInst(state, donateInst) {
      state.donate.contract = donateInst
    },
    setPoolList(state, poolList) {
      state.poolList = poolList
    },
    setTradeSetting(state, setting) {
      state.tradeInfo.setting = setting
    },
    setTradePoolItem(state, pool) {
      state.tradeInfo.poolItem = pool
    },
    setPoolTokenBalance(state, {balance, balanceInUnit}) {
      state.tradeInfo.poolItem.token.balance = balance
      state.tradeInfo.poolItem.token.balanceInUnit = balanceInUnit
    },
    setPoolTokenAllowance(state, allowance) {
      state.tradeInfo.poolItem.token.allowance = allowance
    },
    setTradeInviter(state, inviter) {
      state.tradeInfo.inviter = inviter
    },
    setTradeMarginBalance(state, marginBalance) {
      state.tradeInfo.marginBalance = marginBalance
    },
    setTransactions(state, transactions) {
      state.transactions = transactions
    },
    setConfirmedTxType(state, confirmedTxType) {
      state.confirmedTxType = confirmedTxType
    },
  },
  actions: {
  }
})
