import store from "@/store"

export default {
    poolDeployer() {
        return store.state.factory.contract.poolDeployer()
    },
    tokenDeployer() {
        return store.state.factory.contract.tokenDeployer()
    },
    getDC2CPool(token) {
        return store.state.factory.contract.getDC2CPool(token)
    },
    getDToken(token) {
        return store.state.factory.contract.getDToken(token)
    },
    getInviter(account) {
        return store.state.factory.contract.getInviter(account)
    },
    config() {
        return store.state.factory.contract.config()
    },
    setConfig(org, fee, returnPer, inviterPer, topNum) {
        return store.state.factory.contract.connect(store.state.provider.getSigner()).setConfig(org, fee, returnPer, inviterPer, topNum)
    },
    tokenSize() {
        return store.state.factory.contract.tokenSize()
    },
    getTokens(from, to) {
        return store.state.factory.contract.getTokens(from, to)
    },
    createPool(token) {
        return store.state.factory.contract.connect(store.state.provider.getSigner()).createPool(token)
    }
}
