import Vue from 'vue'
import VueRouter from "vue-router"
import store from '@/store/index'

Vue.use(VueRouter)

const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
    return VueRouterPush.call(this, location).catch(err => err)
}

const VueRouterReplace = VueRouter.prototype.replace
VueRouter.prototype.replace = function replace (location) {
    return VueRouterReplace.call(this, location).catch(err => err)
}

const routes = [
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/home.vue'),
    },
    {
        path: '/order',
        name: 'order',
        component: () => import('@/components/order/recent_order.vue'),
    },
    {
        path: '/rank',
        name: 'rank',
        component: () => import('@/components/rank/rank.vue'),
    },
    {
        path: '/donate',
        name: 'donate',
        component: () => import('@/components/donate/donate.vue'),
    },
    {
        path: '/token',
        name: 'token',
        component: () => import('@/components/token/token.vue'),
    }
]

const router = new VueRouter({
    mode: 'history',
    routes
})

router.beforeEach(async (to, from, next) => {
    await store.commit('setCurrentRoute', to.fullPath)
    await store.commit('setCurrentRouteName', to.name)
    next()
})

export default router
