import {ethers, BigNumber} from 'ethers'
import config from './config'
import store from '@/store'
import erc20 from '@/utils/erc20';
import metamask from '@/utils/metamask'

export default {
    chainIdToHex(chainId) {
        return '0x' + parseInt(chainId).toString(16)
    },
    numberToHex(num) {
        if (typeof num === 'number') {
            num = num.toString()
        }
        return BigNumber.from(num).toHexString()
    },
    stringToHex(text) {
        return ethers.utils.formatBytes32String(text)
    },
    hexToString(hex) {
        return ethers.utils.toUtf8String(hex).replaceAll('\x00', '')
    },
    getAddress(address) {
        try {
            return ethers.utils.getAddress(address)
        } catch (e) {
            return null
        }
    },
    toBigNumber(value) {
        return BigNumber.from(value.toString())
    },
    weiToUnit(value) {
        if (null === value) {
            return 0
        }
        if (null === store.state.tradeInfo.poolItem) {
            return 0
        }
        if (typeof value === 'number') {
            value = BigNumber.from(value.toString())
        }
        const decimals = store.state.tradeInfo.poolItem.token.decimals
        try {
            return ethers.utils.formatUnits(value, decimals)
        } catch (e) {
            return 0
        }
    },
    weiToUnitWithDecimals(value, decimals) {
        if (null === value) {
            return 0
        }
        return ethers.utils.formatUnits(value, decimals)
    },
    unitToWei(value) {
        if (value === '') {
            return BigNumber.from(0)
        }
        if (null === store.state.tradeInfo.poolItem) {
            return BigNumber.from(0)
        }
        const decimals = store.state.tradeInfo.poolItem.token.decimals
        try {
            return ethers.utils.parseUnits(value.toString(), decimals)
        } catch (e) {
            return BigNumber.from(0)
        }
    },
    unitToWeiWithDecimals(value, decimals) {
        if (value === '') {
            return BigNumber.from(0)
        }
        if (decimals === 0) {
            return BigNumber.from(0)
        }
        return ethers.utils.parseUnits(value.toString(), decimals)
    },
    getNetworkById(id) {
        for (let i = 0; i < config.networks.length; i++) {
            if (id === config.networks[i].id) {
                return config.networks[i]
            }
        }
        return null
    },
    getNetVersionList() {
        return config.networks
    },
    getNetVersionName(netVersion) {
        let name = 'Unknown'
        config.networks.forEach(item => {
            if (item.id === netVersion) {
                name = item.name
            }
        })
        return name
    },
    getNetVersionIcon(netVersion) {
        let icon = require('../assets/ethereum.jpeg')
        config.networks.forEach(item => {
            if (item.id === netVersion) {
                icon = item.icon
            }
        })
        return icon
    },
    getNetVersionBlockInterval(netVersion) {
        let blockInterval = 0
        config.networks.forEach(item => {
            if (item.id === netVersion) {
                blockInterval = item.blockInterval
            }
        })
        return blockInterval
    },
    getExplorerUrl(type, info) {
        let url = ''
        for (let i = 0; i < config.networks.length; i++) {
            if (config.networks[i].id === store.state.netVersion) {
                url = config.networks[i].explorer
                break
            }
        }

        if (type === 'address') {
            return url + '/address/' + info
        } else if (type === 'token') {
            return url + '/token/' + info
        } else if (type === 'tx') {
            return url + '/tx/' + info
        }
        return ''
    },
    getFactoryAddress() {
        const network = this.getNetworkById(store.state.netVersion)
        if (null === network) {
            return ''
        }
        return network.factoryAddress
    },
    getDonateAddress() {
        const network = this.getNetworkById(store.state.netVersion)
        if (null === network) {
            return ''
        }
        return network.donateAddress
    },
    getConfigTokens() {
        const network = this.getNetworkById(store.state.netVersion)
        if (null === network) {
            return []
        }
        return network.tokens
    },
    getContractInst(address, abi) {
        return new ethers.Contract(address, abi, store.state.provider)
    },
    objCopy(source, target) {
        for (let field in source) {
            target[field] = source[field]
        }
    },
    getTokenByAddress(address) {
        if (!address) {
            return null
        }
        for (let i = 0; i < store.state.poolList.length; i++) {
            if (store.state.poolList[i].token.address.toLowerCase() === address.toLowerCase()) {
                return store.state.poolList[i].token
            }
        }

        return null
    },
    getPoolByAddress(address) {
        for (let i = 0; i < store.state.poolList.length; i++) {
            if (store.state.poolList[i].address.toLowerCase() === address.toLowerCase()) {
                return store.state.poolList[i]
            }
        }
        return null
    },
    formatLongText(text) {
        let first = text.substring(0, 6)
        let mid = '...'
        let last = text.substring(text.length - 4, text.length)
        return first + mid + last
    },
    getEmptyAddress() {
      return '0x0000000000000000000000000000000000000000'
    },
    getTxTypes() {
        return config.txTypes
    },
    getTxStatus(tx) {
        if (!tx.receipt || !tx.receipt.blockNumber) {
            return 'pending'
        }
        if (!tx.receipt.status) {
            return 'failed'
        }
        return 'success'
    },
    async getAccount(message, func) {
        if (typeof window.ethereum === 'undefined') {
            func()
            return
        }
        const account = await metamask.getAccount()
        await store.commit('setAccount', account)
        await this.syncAccountTokenInfo()
    },
    async syncAccountTokenInfo() {
        await erc20.syncTokenBalance()
        await erc20.syncTokenAllowance()
    },
    async onTokenSelected(poolInst) {
        if (!poolInst) {
            await store.commit('setTradePoolItem', null)
            return
        } else {

            await store.commit('setTradePoolItem', poolInst)
        }

        // await this.syncAccountTokenInfo()
    },
    getQueryParam(name) {
        const query = window.location.search.substring(1)
        const vars = query.split('&')
        for (let i = 0; i < vars.length; i++) {
            const pair = vars[i].split('=')
            if (pair[0] === name) {
                return pair[1]
            }
        }
        return (false)
    }
}
