import store from '@/store'
import {ethers} from "ethers"
import common from "@/utils/common";

export default {
    balanceOf(ownerAddress) {
        return store.state.tradeInfo.poolItem.token.contract.balanceOf(ownerAddress)
    },
    allowance(owner, spender) {
        return store.state.tradeInfo.poolItem.token.contract.allowance(owner, spender)
    },
    approve(to, amount) {
        if (amount < 0) {
            amount = ethers.utils.parseUnits('1', 65)
        }
        return store.state.tradeInfo.poolItem.token.contract.connect(store.state.provider.getSigner()).approve(to, amount)
    },
    async syncTokenBalance() {
        const account = store.state.account
        if (account === '') {
            return
        }

        if (store.state.tradeInfo.poolItem === null) {
            return
        }

        const balance = await this.balanceOf(account)
        const balanceInUnit = common.weiToUnit(balance)

        await store.commit('setPoolTokenBalance', {
            balance: balance,
            balanceInUnit: balanceInUnit
        })
    },
    async syncTokenAllowance() {
        const account = store.state.account
        if (account === '') {
            return
        }
        const poolItem = store.state.tradeInfo.poolItem
        const allowance = await this.allowance(account, poolItem.address)
        await store.commit('setPoolTokenAllowance', allowance)
    },
}
